@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);

.Badge_container {
    background: #fff;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5);
    border-radius: 8px 8px 8px 8px;
    overflow: hidden;
    height: 380px;
}

.Badge_banner {
    padding: 0.7rem 0;
    height: 80px;
    background: #1b1b25;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;

}

.Badge_profile {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem 0;
}

.Badge_profile_photo img {
  border-radius: 50%;
  margin: auto 2rem;
  width: 120px;
  height: 120px;
}

.Badge_profile_about h1 {
    
    font-size: 2.3rem ;
    margin: auto 2rem;
    text-align: center;
}

.Badge_job {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 0.5rem 0;
  background: #f4f4f7;
}
.Badge_job_tittle  {
    font-weight: 400;
    font-size: 1.8rem ;
    margin: 1rem 2rem;
    text-align: center;
}
.Badge_job_account {
    font-weight: 300;
    font-size: 1.4rem ;
    margin: 0 2rem;
    text-align: center;
}

.Badge_footer {
    height: 54px;
    margin-top: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: #98ca3f;
    font-weight: bold;
    font-style: italic;
}
.Badge_footer p {
    font-weight: 400;
    font-size: 1.4rem ;
}
.BadgeForm {
    position: relative;
    bottom: 120px;
    margin: 15px 10px;
    overflow: hidden;
    min-height: 500px;
}

.BadgeForm h2 {
    color: #f8f5f1;
    text-align: center;
    font-size: 3rem;
    line-height: 5rem;
}

.BadgeForm_form {
    margin: 10px 20px;
    padding: 15px;
    background: #fff;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5);
    border-radius: 8px 8px 8px 8px;
    overflow: hidden;

}

.BadgeForm_form_label {
    font-weight: 700;
    font-size: 1.8rem;
}

.BadgeForm .BadgeForm_form .BadgeForm_form_input {
    background-color: #f8f5f1;
    font-weight: 300;
    font-size: 1.3rem;
    margin-bottom: 30px;
}

.BadgeForm .BadgeForm_form .BadgeForm_form_button {
    vertical-align: center;
    background: #7DCD40;
    color: #f8f5f1;
    margin-top: 10px;
    font-weight: 700;
    font-size: 1.8rem;
    max-width: 300px;
    min-width: 100px;
    width: 200px;

}

.BadgeForm .BadgeForm_form .BadgeForm_form_button:hover {
    background: #1e5372;
}

@media (max-width: 767px) {
    .BadgeForm {
        margin-top: 180px;
    }

    .BadgeForm .BadgeForm_form .BadgeForm_form_button {
        margin: 20px 55px;
    }

    .BadgeForm h2 {
        color: #7DCD40;
    }
}
.badgeNew_hero {
